@import '~assets/styles/h3Imports';

.root {
  align-items: center;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  padding: rem(24) 0;
  width: 100%;

  & > * {
    display: inline-flex;
    justify-content: center;
    margin-bottom: rem(4);
    text-align: center;
  }

  :first-child {
    margin-bottom: 0;

    --icon-stroke: var(--gray-3);

    svg {
      height: rem(60);
      margin-bottom: 0;
      width: rem(60);
    }
  }

  :nth-child(2) {
    color: var(--gray-3);
    font-weight: 700;
  }

  :nth-child(3) {
    color: var(--gray-3);
    font-weight: 400;
  }
}
